import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import statusBar from '../home/statusBar.vue'
import TransferDom from 'vux/src/directives/transfer-dom/'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
export default {
  data () {
      return {
          title: '',
          orderTitle: '',
          dialogShow: false,
          dialogMsg: '',
          dialogLink: '',
          order_type: '',
          step: 1,
          goods_name: '',
          content_all: {
              desc1: '',
              time1: '',
              desc2: '',
              time2: '',
              desc3: '',
              time3: ''
          },
          show_revoke:false,
          revoke_status:'',
          norevoke:'',
          revoke_id:'',
          confirmVisible: false
      }
  },
  computed: {
      ...mapGetters(['company'])
  },
  directives: {
      TransferDom
  },
  components: {
      XHeader,
      Group,
      Flexbox,
      FlexboxItem,
      XButton,
      statusBar,
      XDialog,
      Confirm
  },
  created () {
    if (this.$route.query.order_type === 'chongzhi') {
        this.orderTitle = ''
        document.title = '-'
    } else if (this.$route.query.order_type === 'goumai') {
        this.orderTitle = ''
        document.title = '-'
    } else if (this.$route.query.order_type === 'shuhui' || this.$route.query.order_type === 'shuhui_saving') {
        this.orderTitle = ''
        document.title = '-'
    } else if (this.$route.query.order_type === 'yuyue_shuhui') {
        this.orderTitle = ''
        document.title = '-'
    } else if (this.$route.query.order_type === 'tixian') {
        this.orderTitle = ''
        document.title = '-'
    } else if (this.$route.query.order_type === 'transfer') {
        this.orderTitle = ''
        document.title = '-'
    }
    window.scroll(0, 0)
    this.$nextTick(() => {
        this.bao_orderDetail()
    })
  },
  methods: {
      bao_orderDetail () {
          let form = new FormData()
          form.append('id',this.$route.query.id)
          form.append('order_type', this.$route.query.order_type)
          this.$store.dispatch('bao_orderDetail', form).then((res) => {
              this.show_revoke = res.show_revoke
                  this.revoke_status = res.status
                  this.norevoke = res.norevoke
                  this.revoke_id = res.revoke_id
              this.order_type = res.order_type
              this.step = res.step
              if (res.order_type === 'chongzhi' || res.order_type === 'chongzhi_offline') {
                  this.title = res.order_type_desc
                  this.step = 2
                  this.content_all.desc1 = ''
                  this.content_all.desc2 = ''
                  this.content_all.time2 = res.update_time
              } else if (res.order_type === 'goumai') {
                  this.title = '' + res.amount + ''
                  this.goods_name = res.trade_type_desc
                  this.content_all.desc1 = ''
                  this.content_all.time1 = res.create_time
                  this.content_all.desc2 = res.msg2 + ''
                  this.content_all.time2 = res.jixi
                  if (res.benxishouyi) {
                      var benxi = ''
                  } else {
                      benxi = ''
                  }
                  this.content_all.desc3 = benxi + ''
                  this.content_all.time3 = res.xianshi
              } else if (res.order_type === 'tixian') {
                  this.title = ' ' + res.amount + ' ' + res.result
                  this.content_all.desc1 = ''
                  this.content_all.time1 = res.create_time
                  this.content_all.desc2 = res.msg
                  this.content_all.time2 = res.daozhang
              } else if (res.order_type === 'shuhui' || res.order_type === 'shuhui_saving') {
                  if (res.bufenshuhui) {
                      var shuhui = ''
                  } else {
                      shuhui = ''
                  }
                  this.title = shuhui + ' ' + res.amount + ' ' + res.result
                  this.goods_name = res.trade_type_desc
                  this.content_all.desc1 = ''
                  this.content_all.time1 = res.create_time
                  this.content_all.desc2 = '' + res.payee_account_desc
                  this.content_all.time2 = (res.msg?res.msg:'') + res.daozhang
              } else if (res.order_type === 'yuyue_shuhui') {
                  this.title = ' ' + res.amount + ' ' + res.result
                  this.goods_name = res.trade_type_desc
                  this.content_all.desc1 = ''
                  this.content_all.time1 = res.create_time
                  if (res.chexiao) {
                      var yuyueshuhui = ''
                  } else {
                      yuyueshuhui = ''
                  }
                  this.content_all.desc2 = yuyueshuhui
                  this.content_all.time2 = (res.msg?res.msg:'') + res.daozhang
              } else if (res.order_type === 'transfer') {
                  this.title = ' ' + res.amount + ' ' + res.result
                  this.goods_name = res.trade_type_desc
                  this.content_all.desc1 = ''
                  this.content_all.time1 = res.create_time
                  this.content_all.desc2 = ''
                  this.content_all.time2 = res.jixi
              }
          }).catch((res) => {
              this.dialogMsg = res.errmsg
              this.dialogShow = true
              if (res.errcode === 40001) {
                  this.dialogLink = '/login'
              }
          })
      },

      submit () {
          this.showConfirm = true
      },
      onConfirm(){
          this.$store.dispatch('bao_ajax_revoke_yuyue_shuhui', {'id_code':this.revoke_id,'rd':Math.random}).then((res) => {
              this.revoke_status = 4
          }).catch((res) => {
              if(res.errcode==40011){
                  this.dialogMsg = res.errmsg
                  this.showDialog = true
              }
          })
      }
  }
}



